import Splide from '@splidejs/splide';

const EL = '.js-show'
const ACTIVE_CLASS = 'is-active'

if (document.querySelector(EL)) {
  const slider = new Splide( EL, {
    type: 'fade',
    rewind: true,
    perPage: 1,
    pagination: true,
    arrows: true,
    speed: 1000,
  } ).mount();
}

if (document.querySelector('.js-thumbnails')) {
  new Splide( '.js-thumbnails', {
    fixedWidth : 'calc(25% - 23px)',
		rewind     : true,
		pagination : false,
    isNavigation: true,
    arrows: false,
    perPage     : 4,
    breakpoints: {
  		767: {
  			perPage:2,
        fixedWidth : 'calc(50% - 7px)',
  		},
    }
  } ).mount();

  new Splide( '.js-main', {
    type      : 'fade',
    rewind    : true,
    pagination: false,
    arrows    : false,
    perPage: 1,
  } ).mount();

  var main       = new Splide( '.js-main', { type: 'fade', rewind: true, pagination: false, arrows: false, perPage: 1, } );
  var thumbnails = new Splide( '.js-thumbnails', {fixedWidth : 'calc(25% - 23px)',rewind: true, pagination: false, arrows: false, isNavigation: true, perPage: 4,breakpoints: { 767: { perPage:2, pagination: false,fixedWidth : 'calc(50% - 7px)', }, } } );

  main.sync( thumbnails );
  main.mount();
  thumbnails.mount();
}
